'use client';

import { useState, useMemo } from 'react';
import StatsCard from '@/components/StatsCard';
import SearchBar from '@/components/SearchBar';
import DataTable from '@/components/DataTable';
import StatusChart from '@/components/StatusChart';
import styles from './DashboardClient.module.css';

export default function DashboardClient({ headers, rows, stats }) {
    const [searchTerm, setSearchTerm] = useState('');
    const [activeFilter, setActiveFilter] = useState(null);

    const filteredRows = useMemo(() => {
        let result = rows;

        // Apply status filter
        if (activeFilter) {
            result = result.filter(row => {
                const rowText = row.join(' ').toLowerCase();

                if (activeFilter === 'approved') {
                    return rowText.includes('approved') || rowText.includes('confirmed');
                } else if (activeFilter === 'pending') {
                    return rowText.includes('pending');
                } else if (activeFilter === 'notSubmitted') {
                    return rowText.includes('not submitted') || rowText.includes('not paid');
                }

                return true;
            });
        }

        // Apply search filter
        if (searchTerm) {
            result = result.filter(row => {
                const searchLower = searchTerm.toLowerCase();
                return row.some(cell =>
                    cell && cell.toString().toLowerCase().includes(searchLower)
                );
            });
        }

        return result;
    }, [rows, searchTerm, activeFilter]);

    const handleFilterClick = (filterType) => {
        setActiveFilter(activeFilter === filterType ? null : filterType);
    };

    return (
        <>
            {/* Stats Grid */}
            <div className={styles.statsGrid}>
                <StatsCard
                    label="Total Students"
                    value={stats.total}
                    icon="ðŸ‘¥"
                    color="blue"
                    onClick={() => setActiveFilter(null)}
                    isActive={activeFilter === null}
                />
                <StatsCard
                    label="Approved"
                    value={stats.approved}
                    icon="âœ“"
                    color="green"
                    onClick={() => handleFilterClick('approved')}
                    isActive={activeFilter === 'approved'}
                />
                <StatsCard
                    label="Pending"
                    value={stats.pending}
                    icon="â³"
                    color="orange"
                    onClick={() => handleFilterClick('pending')}
                    isActive={activeFilter === 'pending'}
                />
                <StatsCard
                    label="Not Submitted"
                    value={stats.notSubmitted}
                    icon="âœ—"
                    color="red"
                    onClick={() => handleFilterClick('notSubmitted')}
                    isActive={activeFilter === 'notSubmitted'}
                />
            </div>

            {/* Charts Section */}
            <div className={styles.chartsGrid}>
                <StatusChart stats={stats} />
                <div className={styles.chartContainer}>
                    <h3 className={styles.chartTitle}>Quick Stats</h3>
                    <div className={styles.statsInfo}>
                        <div className={styles.statRow}>
                            <span className={styles.statLabel}>Total Students</span>
                            <span className={styles.statValue}>{stats.total}</span>
                        </div>
                        <div className={styles.statRow}>
                            <span className={styles.statLabel}>Approval Rate</span>
                            <span className={styles.statValue}>
                                {stats.total > 0 ? Math.round((stats.approved / stats.total) * 100) : 0}%
                            </span>
                        </div>
                        <div className={styles.statRow}>
                            <span className={styles.statLabel}>Pending Rate</span>
                            <span className={styles.statValue}>
                                {stats.total > 0 ? Math.round((stats.pending / stats.total) * 100) : 0}%
                            </span>
                        </div>
                        <div className={styles.statRow}>
                            <span className={styles.statLabel}>Incomplete</span>
                            <span className={styles.statValue}>
                                {stats.total > 0 ? Math.round((stats.notSubmitted / stats.total) * 100) : 0}%
                            </span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Search Bar */}
            <div className={styles.searchSection}>
                <SearchBar
                    onSearch={setSearchTerm}
                    placeholder="Search by name or registration number..."
                />
                <div className={styles.resultCount}>
                    {activeFilter && (
                        <span className={styles.filterBadge}>
                            Filtered: {activeFilter === 'approved' ? 'Approved' :
                                activeFilter === 'pending' ? 'Pending' :
                                    'Not Submitted'} â€¢{' '}
                        </span>
                    )}
                    Showing {filteredRows.length} of {rows.length} students
                </div>
            </div>

            {/* Data Table */}
            <DataTable headers={headers} rows={filteredRows} />
        </>
    );
}
